import React, { Component } from 'react';
import SVG from 'react-inlinesvg';
import './Appearance.css';
import * as utils from '../../utils';
import * as api from '../../services/api';
import connect from "../../libs/vkui-connect";

export default class Appearance extends Component {
  constructor(props) {
    super(props);

    this.state = {
      color: window.color || 'purple',
      theme: window.theme || 'vk'
    };
  }

  render() {
    return (
      <div className="Page__wrap">
        <div className="Appearance__title">Тема</div>
        <div className="Appearance__themes">
          {this._renderThemes()}
        </div>
        <div className="Appearance__title">Цвет</div>
        <div className="Appearance__palette_items">
          {this._renderPalette()}
        </div>
      </div>
    )
  }

  _renderPalette() {
    return [
      'red',
      'orange',
      'green',
      'cyan',
      'blue',
      'purple',
      'pink',
      'grey'
    ].map((color) => {
      const className = utils.classNames({
        Appearance__palette_item: true,
        [color]: true,
        selected: this.state.color === color
      });
      return (
        <div
          key={color}
          className={className}
          onClick={() => {
            this.setState({color});
            utils.applyColor(color);
            api.method(api.methods.saveColor, {color}).catch((err) => console.log(err));
            connect.send('VKWebAppTapticSelectionChanged', {});
          }}
        >
          <div className="Appearance__palette_item_selected">
            <SVG src={require('../../asset/done_24.svg')} />
          </div>
        </div>
      )
    });
  }

  _renderThemes() {
    let themes = [{
      label: 'Светлая',
      name: 'light'
    }, {
      label: 'Темная',
      name: 'dark'
    }];
    if (!window.isNative) {
      themes.unshift({
        label: 'Как в ВК',
        name: 'vk'
      });
    }

    return themes.map((theme) => {
      const className = utils.classNames({
        Appearance__theme: true,
        selected: this.state.theme === theme.name
      });
      return (
        <div
          className={className}
          key={theme.name}
          onClick={() => {
            if (window.theme !== theme.name) {
              api.method(api.methods.saveTheme, {theme: theme.name}).catch((err) => console.log(err));
            }
            this.setState({theme: theme.name});
            utils.applyTheme(theme.name);
            connect.send('VKWebAppTapticSelectionChanged', {});
          }}
        >
          <div className="Appearance__theme__label">{theme.label}</div>
        </div>
      )
    });
  }
}



